'use client'
export default async function SideBar() {
const apiKey = process.env.RAWG_API_KEY;
    try {
        const fetchLastGame = await fetch(` https://api.rawg.io/api/games?key=${apiKey}&ordering=-released&page_size=1`)
        const data =  fetchLastGame.json()
        console.log(data)
    } catch(error){
        console.log(error)
    }
    return(
        <h1>hello</h1>
    )
}